import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { fillDummyQuestions } from "redux/slices/contentSlice";
import { AdminContent, AdminTitle } from "./components";
import { Button, Space } from "antd";

export const AdminPage = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // ''     
  const onClickPreview = () => {
    navigate("/preview");
  };

  // '  '     
  const onClickFillDummyData = () => {
    dispatch(fillDummyQuestions());
  };

  return (
    <section
      id="form-container"
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        gap: 30,
        paddingTop: 50,
        paddingBottom: 50,
        position: "relative",
      }}
    >
      <Space>
        <Button type="primary" onClick={onClickPreview}>
          
        </Button>
        <Button onClick={onClickFillDummyData}>  </Button>
      </Space>
      <AdminTitle />
      <AdminContent />
      <Button type="primary" onClick={onClickPreview}>
        
      </Button>
    </section>
  );
};
